@mixin homeMax {
  @media (max-width: 1620px) {
    @content;
  }
}

@mixin dMax {
	@media (max-width: 1500px) {
		@content;
	}
}

@mixin tMax {
	@media (max-width: 1300px) {
		@content;
	}
}

@mixin sMax {
	@media (max-width: 1100px) {
		@content;
	}
}

@mixin mMax {
	@media (max-width: 768px) {
		@content;
	}
}

@mixin mobile {
	@media (max-width: 450px) {
		@content;
	}
}

@mixin mMin {
	@media (min-width: 769px) {
		@content;
	}
}





$resolutiond: 1920;


@function vwd($px) {
	@return (($px/$resolutiond) * 100)+vw;
}


$resolution: 375;


@function vw($px) {
	@return (($px/$resolution) * 100)+vw;
}


$resolutiond2: 1620;

@function qw($px) {
  @return (($px/$resolutiond2) * 100)+vw;
}


$resolutiond3: 1110;

@function qw2($px) {
  @return (($px/$resolutiond3) * 100)+vw;
}
