.target-features {
    position: relative;
    padding-top: 122px;
    padding-bottom: 209px;
    overflow: hidden;
    &__container {
        color: #000;
        position: relative;
    }
    
    &__content {
        padding: 0 0 0 84px;
    }
    
    &__row {
        display: flex;
        align-items: center;
        position: relative;
        justify-content: space-between;
        &:nth-child(1) {
            margin-bottom: 118px;

        }
        &:nth-child(2) {
            margin-bottom: 74px;
        }
        &:nth-child(3) {
            margin-bottom: 145px;
            
        }
    }
    &__img-wrapper {
        position: relative;
        width: 100%;
        
    }
    &__text {
        font-size: 64px;
        line-height: 125.8%;
        max-width: 757px;
    }
    &__text-one {
        max-width: 608px;
        padding-top: 72px;
    }
    
    &__img-wrapper-one {
        max-width: 753px;
        margin-left: -26px;
        width: 100%;
        margin-right: 50px;
        &::before {
            content: '';
            display: block;
            padding-top: 630/753 * 100%;
        }
    }
    
    &__img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: center center;
    }
    &__img-mob {
        display: none;
    }

    &__img-wrapper-two {
        margin-right: 40px;
        max-width: 639px;
        width: 100%;
        margin-left: 50px;
        &::before {
            content: '';
            display: block;
            padding-top: 223/639 * 100%;

        }
    }
    
    &__text-two {
        margin-top: 48px;
        padding-left: 26px;
    }
    
    &__text-three {
        max-width: 565px;
        margin-right: 44px;
    }
    
    &__img-wrapper-three {
        margin-left: -88px;
        max-width: 761px;
        margin-right: 50px;
        width: 100%;
        &::before {
            content: '';
            display: block;
            padding-top: 401/761 * 100%;
        }
    }
    }
    